import { EmptyStateWatchlists } from "@rp/react/common/assets"
import { LinkStyled } from "@rp/react/web/common/base-components"
import { Box, Typography } from "@rp/react/web/mui/core"

type Props = {
  onBrowse: () => void
  canEdit: boolean
}

export const EmptyWatchlistTable = ({ onBrowse, canEdit }: Props) => {
  return (
    <Box
      sx={{
        display: "flex",
        alignItems: "center",
        height: "calc(100vh - 350px)",
      }}
    >
      <Box sx={{ width: "100%", textAlign: "center" }}>
        <EmptyStateWatchlists />
        <Box sx={{ mt: 4 }}>
          <Typography sx={{ fontSize: 20, fontWeight: 600, textAlign: "center" }}>
            {canEdit ? "Your Watchlist is empty" : "This Watchlist is empty"}
          </Typography>
          {canEdit && (
            <Typography sx={{ fontSize: 16, color: "grey.300" }}>
              Start adding entities to your watchlist by typing in the search <br />
              bar or{" "}
              <LinkStyled
                to="/"
                onClick={ev => {
                  ev.preventDefault()
                  onBrowse()
                }}
              >
                upload a list
              </LinkStyled>{" "}
              from your device
            </Typography>
          )}
        </Box>
      </Box>
    </Box>
  )
}
